.header {
  position: absolute;
  top: 65px;
  left: 0;
  width: 100%;
  z-index: 2;

  &__container {
    display: flex;
    justify-content: space-between;
    align-content: center;

    @include desktop {
      padding-left: 30px;
      padding-right: 30px;
    }
  }

  &__contacts {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex: 2 1 auto;

    @include desktop {
      flex: 1 1 auto;
      justify-content: flex-end;
    }

    @include big-mobile {
      display: none;
      justify-content: flex-start;
      align-items: flex-start;
    }
  }

  &__mail {
    font-weight: 400;
    font-size: 15px;
    line-height: 17px;
    color: var(--light-color);
    transition: opacity .3s ease-in-out;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: -3px;
      left: -34px;
      width: 35px;
      height: 32px;
      background-image: url(../img/header-mail.svg);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 0 0;
    }

    &:hover {
      opacity: 0.7;
    }

    @include desktop {
      margin-right: 50px;
    }

    @include tablet {
      margin-right: 32px;
    }
  }

  &__tel {
    font-weight: 700;
    font-size: 15px;
    line-height: 17px;
    text-transform: uppercase;
    color: var(--light-color);
    transition: opacity .3s ease-in-out;
    position: relative;
    margin-left: 10px;

    &::before {
      content: '';
      position: absolute;
      top: 3px;
      left: -20px;
      width: 12px;
      height: 13px;
      background-image: url(../img/header-tel.svg);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 0 0;
    }

    &:hover {
      opacity: 0.7;
    }

    @include desktop {
      margin-right: 50px;
    }

    @include tablet {
      margin-right: 32px;
    }
  }

  &__social {
    display: flex;
    align-items: flex-start;
  }

  &__social-item {
    width: 19px;
    height: 19px;
    background-color: var(--light-color);
    border-radius: 50%;

    transition: opacity .3s ease-in-out;

    &:hover {
      opacity: 0.7;
    }

    &:not(:last-child) {
      margin-right: 7px;
    }
  }

  &__social-link {
    display: block;
    transform: translate(50%, 0);

    &--tg {
      width: 100%;
      height: 100%;
      transform: translate(19%, -4%);
    }

    &--wa {
      width: 100%;
      height: 100%;
      transform: translate(25%, -4%);
    }

    &--vb {
      width: 100%;
      height: 100%;
      transform: translate(24%, 0%);
    }
  }
}

.logo__image {
  @include big-desktop {
    width: 108px;
  }
}
